import React from 'react';
import Logo from './Logo';
const Contact=()=>{
    return (
        <div>
        <Logo />
        <div className='contact tc'>
            <h1 className='f1 grow grey underline pa3 pointer'>Contact Us</h1>
            <p className='tc f2  grey pa3 pointer'>Thank you for your interest in **INSERT COMPANY NAME** Pvt. Ltd. Please contact us using the information below. To locate, contact in the Leadtech office (Gurgaon), visit our official websites. For additional information on our various consulting services or our industry expertise, please visit the appropriate page on our site.</p>
        </div>
    </div>
)
}
export default Contact;