import React from 'react';
import Tilt from 'react-tilt';
import Speaker from './fist.jpg';

const Logo=()=>{
    return(
        <div >


        <Tilt className="Tilt br2 shadow-2" options={{ max : 25 }} style={{ height: 250, width: 250 }} >
        <div className="Tilt-inner"> <img style={{paddingTop:'30px', paddingLeft:'25px'}} alt='logo' src={Speaker}/></div>
         </Tilt>

        </div>

    )
}
export default Logo;