import React from 'react';
import {Link} from'react-scroll';
import {Link as Linker} from 'react-router-dom';
const Nav=()=>{
    return (
        <div id="navbar">
        <nav class="navbar navbar-expand-lg navbar-dark bg-dark">

        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
        </button>

        <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav mr-auto">
        <li class="nav-item ">
        <a class="navbar-brand nav-link" href="#"><Linker to="/">Home</Linker> <span class="sr-only">(current)</span></a>
    </li>

        <li class="nav-item ">
        <a class="navbar-brand nav-link" href="#"><Linker to="/About">About Us</Linker> <span class="sr-only">(current)</span></a>
    </li>



        <li class="nav-item dropdown">
        <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
        Our Services
        </a>
        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
        <a class="dropdown-item" href="#"><Linker to="/Pr">Political Reaserch</Linker></a>
        <a class="dropdown-item" href="#"><Linker to="/Pc">Political Canvassing</Linker></a>
    <div class="dropdown-divider"></div>
        <a class="dropdown-item" href="#">Something else here</a>
    </div>
    </li>
    <li class="nav-item active">
        <a class="nav-link " href="#" tabindex="-1" aria-disabled="true"><Linker to="/Contact">Contact Us</Linker></a>
    </li>

    </ul>
    <form class="form-inline my-2 my-lg-0">
        <input class="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search"/>
        <button class="btn btn-outline-success my-2 my-sm-0" type="submit">Search</button>
        </form>
        </div>
        </nav>
        </div>
    )
}
export default Nav;